import { NavLink } from "react-router-dom";
import Cartimage from "./Asset/shopping-bag.png";
const Navbar = () => {
  const gray = {
    color: "gray",
    textDecoration: "underLine",
    textDecorationColor: "salmon",
    textDecorationtThickness: "0.75em"
  };
  const black = {
    color: "black"
  };

  const link = [
    {
      title: "Logo",
      to: "#"
    },
    {
      title: "Home",
      to: "/"
    },
    {
      title: "Signin",
      to: "/login"
    }
  ];
  return (
    <div className="navbar">
      <div className="lnav">
        {link.map((link) => (
          <NavLink
            style={({ isActive }) => (isActive ? gray : black)}
            key={link.title}
            to={link.to}
          >
            {link.title}
          </NavLink>
        ))}
      </div>
      <div className="rnav">
        <NavLink to="/cart">
          <img src={Cartimage} alt="" />
        </NavLink>
      </div>
    </div>
  );
};
export default Navbar;
